@font-face {
  font-display: swap;
  font-family: "Brandon Grotesque";
  font-style: normal;
  font-weight: 500;
  src: url("../fonts/BrandonGrotesque-Bold.woff2");
}
@font-face {
  font-display: swap;
  font-family: "Brandon Grotesque Bold";
  font-style: normal;
  font-weight: 700;
  src: url("../fonts/BrandonGrotesque-Bold.woff2");
}
:root {
  --primary-color: #314460;
  --secondary-color: #B9B19C;
  --tertiary-color: #B7B677;
  --quaternary-color: #B7B677;
  --white-color: #FFF;
  --black-color: #192132;
  --leading-loose: 34px;
  --gradient-home: linear-gradient(90deg,rgb(49,68,96) 0%,rgb(25,33,50) 100%);
  --wp-preset-font-size-section-header-mobile: 54px;
  --wp-preset-font-size-section-hero-mobile: 76px;
  --container: 1152px;
}

ul li {
  list-style: none;
  line-height: 1.75;
}

html {
  display: table;
}

html,
body {
  min-height: 100%;
  width: 100vw;
  overflow-x: hidden;
}

.wp-site-blocks {
  max-width: var(--container);
  margin: 0 auto;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  height: 100%;
  padding: 0 16px;
}

body {
  background-image: url("../images/lines.svg"), url(data:image/svg+xml;base64,), var(--gradient-home);
  background-repeat: no-repeat, no-repeat, repeat;
  background-position: bottom right, center, top left;
  background-size: 50%, 100%, 100%;
}
@media (max-width: 768px) {
  body {
    background-size: 100%, 100%, 100%;
  }
}

p {
  line-height: var(--leading-loose);
}
p strong {
  font-family: var(--font-bold);
}

.alignwide,
.alignfull {
  margin: 0 calc(50% - 50vw);
  max-width: 100vw;
  width: 100vw;
}

.wp-site-blocks > .wp-block-template-part .header.wp-block-columns {
  align-items: center !important;
  margin-bottom: 0;
  padding-top: 32px;
}
@media (max-width: 768px) {
  .wp-site-blocks > .wp-block-template-part .header.wp-block-columns .wp-block-column .wp-block-site-logo {
    text-align: center;
  }
  .wp-site-blocks > .wp-block-template-part .header.wp-block-columns .wp-block-column .wp-block-navigation ul {
    justify-content: center !important;
  }
}
.wp-site-blocks > .wp-block-template-part .header .wp-block-navigation__responsive-container-open {
  position: absolute;
  right: 16px;
  top: -100px;
}
.wp-site-blocks > .wp-block-template-part .header .wp-block-navigation-item__content:hover span, .wp-site-blocks > .wp-block-template-part .header .wp-block-navigation-item__content[aria-current=page] span {
  color: var(--tertiary-color);
}

.footer .copyright,
.footer .copyright a {
  font-size: 16px;
}

.home,
.page-template-page-about,
.page-template-page-default:not(.page-template-template-categorias) {
  height: 100vh;
  width: 100vw;
}

.page .wp-block-columns .post-title,
.home .wp-block-columns .post-title {
  display: flex;
  flex-direction: column;
  justify-content: center;
}
.page .wp-block-columns .post-title h1,
.page .wp-block-columns .post-title h2,
.home .wp-block-columns .post-title h1,
.home .wp-block-columns .post-title h2 {
  margin: 0;
  line-height: 1;
}
@media (max-width: 768px) {
  .page .wp-block-columns .post-title h2.has-section-header-font-size,
.home .wp-block-columns .post-title h2.has-section-header-font-size {
    font-size: var(--wp-preset-font-size-section-header-mobile) !important;
    text-align: left;
  }
  .page .wp-block-columns .post-title h1.has-text-color,
.home .wp-block-columns .post-title h1.has-text-color {
    font-size: var(--wp-preset-font-size-section-hero-mobile);
    text-align: left;
  }
}
@media (max-width: 600px) {
  .page .wp-block-columns .post-title h1,
.page .wp-block-columns .post-title h2,
.home .wp-block-columns .post-title h1,
.home .wp-block-columns .post-title h2 {
    text-align: center;
  }
}
@media (max-width: 768px) {
  .page .wp-site-blocks,
.home .wp-site-blocks {
    padding-left: 16px;
    padding-right: 16px;
  }
  .page .wp-site-blocks .post-title-wrapper,
.home .wp-site-blocks .post-title-wrapper {
    flex-wrap: nowrap !important;
  }
  .page .wp-site-blocks .post-title-wrapper > .wp-block-column:not(.post-title),
.home .wp-site-blocks .post-title-wrapper > .wp-block-column:not(.post-title) {
    flex-basis: 30% !important;
  }
}
@media (max-width: 640px) {
  .page .wp-site-blocks .post-title-wrapper > .wp-block-column,
.home .wp-site-blocks .post-title-wrapper > .wp-block-column {
    flex-basis: 100% !important;
  }
  .page .wp-site-blocks .post-title-wrapper > .wp-block-column:last-child,
.home .wp-site-blocks .post-title-wrapper > .wp-block-column:last-child {
    display: none;
  }
}

.home .wp-site-blocks {
  background-image: url("../images/shapes.svg");
  background-repeat: no-repeat;
  background-position: 85% 80%;
}
@media (max-width: 640px) {
  .home .wp-site-blocks {
    background-image: none;
  }
}

.page-template-page-about .wp-site-blocks {
  background-image: url("../images/shape-b.svg");
  background-repeat: no-repeat;
  background-position: 85% 80%;
}
@media (max-width: 640px) {
  .page-template-page-about .wp-site-blocks {
    background-image: none;
  }
}

.page-template-template-categorias .wp-site-blocks .post-title .has-tertiary-color {
  color: var(--secondary-color) !important;
}
@media (max-width: 640px) {
  .page-template-template-categorias .wp-site-blocks .post-title-wrapper > .wp-block-column:not(.post-title) {
    display: initial;
  }
  .page-template-template-categorias .wp-site-blocks .post-title-wrapper > .wp-block-column.post-title {
    flex-basis: 100% !important;
    width: 100%;
  }
  .page-template-template-categorias .wp-site-blocks .post-title-wrapper > .wp-block-column:last-child {
    display: none;
  }
  .page-template-template-categorias .wp-site-blocks .post-title-wrapper .wp-block-post-content .wp-block-columns .wp-block-column {
    width: 100%;
    flex-basis: 100% !important;
  }
}

.page:not(.home):not(.page-template-page-about) .wp-site-blocks {
  background-image: url("../images/shape-c.svg");
  background-repeat: no-repeat;
  background-position: 85% 80%;
}
@media (max-width: 640px) {
  .page:not(.home):not(.page-template-page-about) .wp-site-blocks {
    background-image: none;
  }
}